@use "../../styles/remConverter" as r;
@use '../../styles/variables' as v;

::-webkit-scrollbar 
{
        display:none;
}

.recapWrapper
{
    width: 100%;
    scroll-margin: 2rem;
}

.imageCardsContainer{
    gap: 1rem;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

.imagewrapper
{
    width: calc((100% - 1 * (v.$imageCardsGap)) / 2);
    height:24rem;
    cursor: pointer;
    @media only screen and (max-width: v.$small-size-desktop-devices) {
        width: calc((100% - 1 * (v.$imageCardsGap)) / 2);
      }
      @media only screen and (max-width: v.$large-size-devices) {
        width: calc((100% - 1 * (v.$imageCardsGap)) / 2);
      }
      @media only screen and (max-width: v.$medium-size-devices) {
        width: calc((100% - 0 * (v.$imageCardsGap)) / 1);
      }
    div
    {
        width:100%;
        height:100%;
        font-size: r.rem(80px) !important;
        div
        {
            height: auto;
            div
            {
                position: absolute;
                left: auto ;
                height: auto;
                right: 2.25rem;
                top: 8rem;
                width: auto;
                font-size: r.rem(80px) !important;
                @media only screen and (max-width: v.$small-size-desktop-devices) {
                    right: 1.75rem;
                    top: 9rem;
                    font-size: r.rem(60px) !important;
                }
                @media only screen and (max-width: v.$large-size-devices) {
                    right: 2rem;
                    top: 9.25rem;
                    font-size: r.rem(50px) !important;
                }
                @media only screen and (max-width: v.$medium-size-devices) {
                    right: 3rem;
                    top: 9rem;
                    font-size: r.rem(50px) !important;
                }
                @media only screen and (max-width: v.$small-size-devices) {
                    top: 9.5rem;
                    right: 2.5rem;
                    font-size: r.rem(40px) !important;
                }
            }
        }
    }
}
